import {React, PageComponent, Page, page} from '../base'
import {toast, IToastResult, IToastOptions} from 'mora-common/widget/Toast'

@page
export class ToastExample extends PageComponent<any, any> {
  instance: IToastResult
  clear() {
    if (this.instance) this.instance.destroy()
  }
  open(message: string | React.ReactNode, options?: IToastOptions) {
    return () => {
      this.clear()
      this.instance = toast(message, {...options, instance: this})
    }
  }
  componentWillUnmount() {
    this.clear()
  }
  render() {
    return (
      <Page name='ToastExample' title={this.app.rp.ToastExample.title}>
        <button className='ct' onClick={this.open(' Toast2s ')}></button>
        <button className='ct' onClick={this.open(' Toast\n   \n2s ', {simpleHTML: true})}></button>
        <button className='ct' onClick={this.open('<a> html  Toast</a>', {html: true})}> html</button>
        <button className='ct' onClick={this.open(<div><a onClick={this.clear}></a></div>, {duration: 0})}> React Node</button>
        <button className='ct' onClick={this.open(' Toast ', {animation: 'zoomIn'})}>zoomIn</button>
      </Page>
    )
  }
}



